import React, { Component } from 'react';
import Flat from './flat';

export default class FlatList extends Component {
  render() {
    const { flats } = this.props;
    return (
      <div className="flat-list">
        { flats.map((flat) => {
          return <Flat flat={flat} key={flat.name} displayMarkerOnMap={this.props.displayMarkerOnMap} removeMarkerFromMap={this.props.removeMarkerFromMap} />
        }) }
      </div>
    );
  }
}
