import React, { Component } from 'react';
// eslint-disable-next-line import/no-extraneous-dependencies
import GoogleMapReact from 'google-map-react';
import flats from '../../data/flats';
import FlatList from './flat_list';
import Marker from './marker';

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedFlat: {
        selectedFlatId: "Charm at the Steps of the Sacre Coeur/Montmartre",
        center: {
          lat: 48.884211,
          lng: 2.34689
        }
      }
    };
  }

  displayMarkerOnMap = (flat) => {
    console.log(flat);
    this.setState({
      selectedFlat: {
        selectedFlatId: flat.name,
        center: {
          lat: flat.lat,
          lng: flat.lng
        }
      }
    });
  }

  removeMarkerFromMap = () => {
    this.setState({
      selectedFlat: {
        selectedFlatId: null,
        center: {
          lat: null,
          lng: null
        }
      }
    });
  }

  render() {
    const { selectedFlat } = this.state;
    return (
      <div>
        <FlatList
          flats={flats}
          selectedFlat={selectedFlat}
          displayMarkerOnMap={this.displayMarkerOnMap}
          removeMarkerFromMap={this.removeMarkerFromMap}
        />
        <div className="map-container">
          <GoogleMapReact
            bootstrapURLKeys={{ key: "" }}
            defaultCenter={selectedFlat.center}
            defaultZoom={15}
          >
            <Marker
              lat={selectedFlat.center.lat}
              lng={selectedFlat.center.lng}
              text={selectedFlat.selectedFlatId}
            />
          </GoogleMapReact>
        </div>
      </div>
    );
  }
}
