import React, { Component } from 'react';

export default class Flat extends Component {
  handleMouseOver = (event) => {
    event.currentTarget.classList.add("active");
    this.props.displayMarkerOnMap(this.props.flat);
  }

  handleMouseLeave = (event) => {
    event.currentTarget.classList.remove("active");
    this.props.removeMarkerFromMap();
  }

  render() {
    const { flat } = this.props;
    return (
      <div onMouseLeave={this.handleMouseLeave} onMouseOver={this.handleMouseOver} className="card" style={{ backgroundImage: `url(${flat.imageUrl})` }}>
        <div className="card-category">{ flat.price } { flat.priceCurrency }</div>
        <div className="card-description">
          <h2>{ flat.name }</h2>
        </div>
        <a className="card-link" href="#"></a>
      </div>
    )
  }
}
